let sliders = document.querySelectorAll('._swiper');
if (sliders) {
	for (let index = 0; index < sliders.length; index++) {
		let slider = sliders[index];
		if (!slider.classList.contains('swiper-bild')) {
			let slider_items = slider.children;
			if (slider_items) {
				for (let index = 0; index < slider_items.length; index++) {
					let el = slider_items[index];
					el.classList.add('swiper-slide');
				}
			}
			let slider_content = slider.innerHTML;
			let slider_wrapper = document.createElement('div');
			slider_wrapper.classList.add('swiper-wrapper');
			slider_wrapper.innerHTML = slider_content;
			slider.innerHTML = '';
			slider.appendChild(slider_wrapper);
			slider.classList.add('swiper-bild');

			if (slider.classList.contains('_swiper_scroll')) {
				let sliderScroll = document.createElement('div');
				sliderScroll.classList.add('swiper-scrollbar');
				slider.appendChild(sliderScroll);
			}
		}
		if (slider.classList.contains('_gallery')) {
			//slider.data('lightGallery').destroy(true);
		}
	}
	sliders_bild_callback();
}

function sliders_bild_callback(params) { }

let sliderScrollItems = document.querySelectorAll('._swiper_scroll');
if (sliderScrollItems.length > 0) {
	for (let index = 0; index < sliderScrollItems.length; index++) {
		const sliderScrollItem = sliderScrollItems[index];
		const sliderScrollBar = sliderScrollItem.querySelector('.swiper-scrollbar');
		const sliderScroll = new Swiper(sliderScrollItem, {
			observer: true,
			observeParents: true,
			direction: 'vertical',
			slidesPerView: 'auto',
			freeMode: true,
			scrollbar: {
				el: sliderScrollBar,
				draggable: true,
				snapOnRelease: false
			},
			mousewheel: {
				releaseOnEdges: true,
			},
		});
		sliderScroll.scrollbar.updateSize();
	}
}

// cлайдер №1
if (document.querySelector('.example-trends__block')) {
	let slider_exampleTrends = new Swiper('.example-trends__block', {

	
		// autoplay: {
		// 	delay: 2000,
		// 	disableOnInteraction: false,
		// },

		slidesPerGroup: 3,
		observer: true,
		observeParents: true,
		slidesPerView: 2,
		spaceBetween: 15,
		speed: 800,
		// Arrows
		navigation: {
			nextEl: '.example-trends__arrows-next',
			prevEl: '.example-trends__arrows-prev',
		},

		breakpoints: {
			320: {
				slidesPerView: 3,
				spaceBetween: 7,
			},
			450: {
				slidesPerView: 2.5,
				spaceBetween: 5,
			},
			550: {
				slidesPerView: 3,
				spaceBetween: 10,
			},
			767: {
				slidesPerView: 4,
				spaceBetween: 15,
			},
			992: {
				slidesPerView: 5,
				spaceBetween: 20,
			},
			1300: {
				slidesPerView: 3,
				spaceBetween: 15,
			},
		},
		on: {
			lazyImageReady: function () {
				ibg();
			},
		}

	});

	// cлайдер №2
	let sliderMainFoto = new Swiper('.mainFoto__slider', {

		effect: 'cards',
		autoplay: {
			delay: 4000,
			disableOnInteraction: false,
		},

		observer: true,
		observeParents: true,
		slidesPerView: 1,
		spaceBetween: 10,
		autoHeight: true,
		speed: 1600,
		loop: true,
		thumbs: {
			swiper: slider_exampleTrends,
		},
				
		on: {
			lazyImageReady: function () {
				ibg();
			},
		}
	});

	sliderMainFoto.on('slideChangeTransitionStart', function () {
		if (window.screen.availWidth < 1300 && window.screen.availWidth > 767) {
			if (document.querySelector('.beautyModels__trends .mainFoto__text-block')) document.querySelector('.beautyModels__trends .mainFoto__text-block').remove();
			let activeMainFotoTextBlock = document.querySelector('.mainFoto__slider .swiper-slide-active .mainFoto__text-block').cloneNode(true);
			let placeToInsertMainFotoTextBlock;
			if (window.screen.availWidth < 768) {
				placeToInsertMainFotoTextBlock = document.querySelector('.trends__link');
			} else placeToInsertMainFotoTextBlock = document.querySelector('.trends__text-row');


			placeToInsertMainFotoTextBlock.after(activeMainFotoTextBlock);
		}
	});
}

